import { useState, useEffect } from "react";
import config from "./config";
import openSocket from "socket.io-client";

let socket = openSocket.connect(config.uri.ENDPOINT, {
  secure: true,
  reconnect: true,
});

/**
 * Send request to the server
 * @param {*} param0
 */
export const sendData = (data, debug = false) => {
  if (debug) console.log(`request: ${data.req}`, data);

  // No data - do not send
  // if (req.length === 0) return false;

  socket.emit(`onDataReceived`, data);
  return true;
};

// Start a session
export const startSession = (data, session, debug = false) => {
  // console.log("startSession");
  if (debug) console.log(`request: start session`, data, session);

  socket.emit(`start session`, {
    data: data,
    session: session,
    req: "start session",
  });
};

// End the session
export const endSession = (data, session, debug = false) => {
  if (debug) console.log(`request: end session`, data, session);
  socket.emit(`end session`, {
    // char: data,
    data: data,
    // uid: uid,
    session: session,
    req: "end session",
  });
};

// Custom hook: useSocket
export const useSocket = (callback) => {
  const [responseData, setResponseData] = useState(null);
  socket.on(`onDataReceived`, (data) => {
    setResponseData(data);
    // callback(data);
  });

  // useEventListener(() => {
  //   socket.on(`onDataReceived`, (data) => {
  //     setResponseData(data);
  //     // callback(data);
  //   });
  // }, ["focus"]);

  useEffect(() => {
    if (responseData === null) return;

    callback(responseData);
    // eslint-disable-next-line
  }, [responseData]);
};
