/**
 * System imports
 ============================================================================ */
import { useEffect } from "react";
// import { useTranslation, Trans } from "react-i18next";
// import { OverlayTrigger, Tooltip } from "react-bootstrap";

export const useEventListener = (callback, events) => {
  let defaults = {
    namespace: "IdleTimer",
    // timeout: 5 * minute,

    onTimeout: () => {},

    events: ["load", "mousemove", "mousedown", "click", "scroll", "keypress"],
  };

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, { events: events });

  // const [gotEvent, setGotEvent] = useState(null);

  useEffect(() => {
    // console.log(props.events);

    const eventHandler = (e) => {
      // setGotEvent(e);
      callback(e);
    };

    // async () => {
    for (let i in props.events) {
      window.addEventListener(props.events[i], eventHandler);
    }

    // };

    return () => {
      for (let i in props.events) {
        // console.log("REMOVE LISTENER");
        window.removeEventListener(props.events[i], eventHandler);
      }
    };
    // eslint-disable-next-line
  }, []);

  /**
       * Return
       ============================================================================ */
  // return gotEvent;
};
