/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Button, Modal } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
// import Card from "../card";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "ModalMessage", message: "", onHide: () => {} };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    return (
      <Modal
        {...props}
        size="sm"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        {false && (
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title-vcenter">
              <img
                src="gfx/zlogo_light.png"
                width="auto"
                height="30"
                className="d-inline-block align-top"
                alt={t("Logo")}
              />
            </Modal.Title>
          </Modal.Header>
        )}
        <Modal.Body>{props.message}</Modal.Body>
        {false && (
          <Modal.Footer>
            <Button onClick={props.onHide}>Close</Button>
          </Modal.Footer>
        )}
      </Modal>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
