/**
 * System imports
 ============================================================================ */
import React, { useState, useEffect } from "react";
import { useTranslation, Trans } from "react-i18next";
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import { AvTimer } from "@material-ui/icons";

/**
   * User imports
   ============================================================================ */

//  let time = new Date().toLocaleString();
import { useInterval } from "../../utils/useInterval";
import { useEventListener } from "../../utils/useEventListener";

import ModalMessage from "../modalMessage";
import SubmitButton from "../submitButton";

/**
 * Default export
 */
export default (args) => {
  /**
     * Constants
     ============================================================================ */
  const minute = 60; // seconds, for testing purposes

  /**
     * Default Properties
     ============================================================================ */
  let defaults = {
    namespace: "IdleTimer",
    timeout: 5 * minute,
    timestamp: null,
    stage2: 60, // show seconds (vs minutes) in stage 2
    stage3: 20, // show modal message on stage 3

    onTimeout: () => {},

    events: ["load", "mousemove", "mousedown", "click", "scroll", "keypress"],
  };

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { formatDate } = props.barrelFunctions;
  const { onSessionTimeout } = props.barrelEventHandlers;

  /**
     * Destructured vars
     ============================================================================ */
  const { t } = useTranslation(props.namespace);

  const [seconds, setSeconds] = useState(props.timeout);
  const [modalMessageShow, setModalMessageShow] = React.useState(false);

  useInterval(() => {
    // Your custom logic here
    setSeconds(seconds - 1);
  }, 1000);

  useEventListener(() => {
    setSeconds(props.timeout);
  }, ["mousedown", "click", "scroll", "keypress"]);

  useEffect(() => {
    if (seconds === props.timeout) setModalMessageShow(false);
    if (seconds === props.stage3) setModalMessageShow(true);
    if (seconds === 0) onSessionTimeout();

    // eslint-disable-next-line
  }, [seconds]);

  const getJSX = () => {
    let currTime;
    if (seconds > props.stage2) currTime = Math.ceil(seconds / minute);
    else currTime = seconds;

    const timeFormatted = formatDate(new Date(1970, 1, 1, 0, 0, currTime), {
      minute: "numeric",
      second: "numeric",
    });

    const onSessionContinue = () => {
      console.log("onSessionContinue");
      // props.timeout = 5 * minute;
      setSeconds(props.timeout);
    };

    const modalMessage = (
      <>
        <Trans
          i18nKey="IdleTimer:Automatic logout"
          timeFormatted={timeFormatted}
        >
          <div className="text-center">Automatic logout in</div>
          <div
            className="text-center"
            style={{ fontSize: "4em", fontWeight: "bold", color: "#A93226" }}
          >
            {{ timeFormatted }}
          </div>
          <div className="text-center">
            Do you want to continue the session?
          </div>
        </Trans>
        <br />
        <SubmitButton
          {...args}
          caption={t("Continue now")}
          request="continue session"
          onSubmit={onSessionContinue}
          namespace={props.namespace}
        />
      </>
    );

    return (
      <>
        <ModalMessage
          show={modalMessageShow}
          message={modalMessage}
          onHide={() => setModalMessageShow(false)}
        />
        <OverlayTrigger
          placement="bottom"
          overlay={
            <Tooltip>
              {seconds > props.stage2 ? (
                <Trans
                  i18nKey="IdleTimer:Minutes to automatical log out"
                  currTime={currTime}
                >
                  You will be automatically logged out in {{ currTime }}{" "}
                  minutes.
                </Trans>
              ) : (
                <Trans
                  i18nKey="IdleTimer:Seconds to automatical log out"
                  currTime={currTime}
                >
                  You will be automatically logged out in {{ currTime }}{" "}
                  seconds.
                </Trans>
              )}
            </Tooltip>
          }
        >
          <div style={{ whiteSpace: "nowrap", cursor: "default" }}>
            <AvTimer style={{ color: "var(--dark)" }} />
            &nbsp;
            {seconds > props.stage2 ? (
              <>
                <span className="d-none d-sm-inline text-muted">
                  <Trans i18nKey="IdleTimer:minutes" currTime={currTime}>
                    {{ currTime }} minutes
                  </Trans>
                </span>
                <span className="d-inline d-sm-none text-muted">
                  <Trans i18nKey="IdleTimer:min" currTime={currTime}>
                    {{ currTime }} min
                  </Trans>
                </span>
              </>
            ) : (
              <>
                <span className="d-none d-sm-inline text-muted">
                  <Trans i18nKey="IdleTimer:seconds" currTime={currTime}>
                    {{ currTime }} seconds
                  </Trans>
                </span>
                <span className="d-inline d-sm-none text-muted">
                  <Trans i18nKey="IdleTimer:sec" currTime={currTime}>
                    <span>{{ currTime }}</span> sec
                  </Trans>
                </span>
              </>
            )}
          </div>
        </OverlayTrigger>
      </>
    );
  };

  return <>{getJSX()}</>;
};
