import { sendData } from "../utils/sockets";

export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {};

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);
  const { req, data, session, uid, barrels } = props;

  // console.log("RESPONSE HANDLER: ", props, barrels);

  /**
   * Barrels
   ============================================================================ */
  const { debug } = barrels.barrelGetters;
  const {
    setUserData,
    setUID,
    setAccountNumber,
    // setUserBusinesses,
  } = barrels.barrelSetters;
  // const { sendRequest, showMessage } = barrels.barrelFunctions;
  //   const {} = barrels.barrelEventHandlers;

  let requestFound = false;

  /** 
   * Request event dispatcher
   ============================================================================ */
  const onRequest = (req, callback) => {
    if (requestFound) return;
    if (req === props.req || req === "default") {
      callback();
      requestFound = true;
    }
  };

  /** 
   * Request handlers
  ============================================================================ */

  onRequest("sign in user", () => {
    // console.log("REQUEST:", req, data, session, uid, barrels);
    // console.log("onRequest:", data);

    setUserData(JSON.stringify(data));
    sendData({ req, data, uid, session }, debug);
  });

  onRequest("register account", () => {
    // console.log("REQUEST:", req, data, session, uid, barrels);
    setUID(data.personalID);
    setAccountNumber(data.personalID);
    sendData({ req, data, uid, session }, debug);
  });

  // onRequest("get user businesses", () => {
  //   // setUserBusinesses(null);
  //   if (userBusinesses === null) {
  //     sendData({ req, data, uid, session });
  //   } else {
  //     //
  //   }
  // });

  // onRequest("register user", () => {
  //   // console.log("REQUEST:", req, data, session, uid, barrels);
  //   // setUserData(JSON.stringify(data));
  //   sendData({ req, data, uid, session });
  // });

  // onRequest("register shareholder", () => {
  //   // console.log("REQUEST:", req, data, session, uid, barrels);
  //   // setUserData(JSON.stringify(data));
  //   sendData({ req, data, uid, session });
  // });

  onRequest("default", () => {
    sendData({ req, data, uid, session }, debug);

    return true;
  });

  // onRequest("unknown", () => {
  //   showMessage(`UNKNOWN REQUEST ${req}`, "info");

  //   return true;
  // });

  return requestFound;
};
