/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
// import { Redirect } from "react-router-dom";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useLocation,
  // Redirect,
} from "react-router-dom";
import {
  Navbar,
  Nav,
  Container,
  Row,
  Col,
  // Button,
  // Breadcrumb,
  // NavDropdown,
  // Alert,
} from "react-bootstrap";
// import Octicon, { SignOut } from "@primer/octicons-react";
import { PowerSettingsNew } from "@material-ui/icons";

/**
  * User imports
  ============================================================================ */
import AccountRegistration from "../../accountRegistration";
import BusinessRegistration from "../../businessRegistration";
import { Balance, Transfer } from "../../transfer";
import MyBusiness from "../../myBusiness";
import UserEditor from "../../userEditor";
import CurrencyContainer from "../../currencyContainer";
import IdleTimer from "../../idleTimer";
// import useIdleTimer from "../../../utils/useIdleTimer";

// const AccountRegistration = React.lazy(() =>
//   import("../../accountRegistration")
// );
// const BusinessRegistration = React.lazy(() =>
//   import("../../businessRegistration")
// );
// const MyBusiness = React.lazy(() => import("../../myBusiness"));
// const UserEditor = React.lazy(() => import("../../userEditor"));
// const CurrencyContainer = React.lazy(() => import("../../currencyContainer"));

/**
 * Component: Summary
 */
export default (args) => {
  // console.log("SUMMARY", args);
  /**
   * Default Properties
   */
  let defaults = { namespace: "Summary" };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   */
  const { accountNumber } = props.barrelGetters;
  const { setGotFocus } = props.barrelSetters;
  const { getBusinesses } = props.barrelFunctions;
  // const {  } = props.barrelEventHandlers;

  const { t } = useTranslation(props.namespace);

  // let tmrUpdateTimestamp = null;

  // React.useEffect(() => {
  //   // if (!signedIn) return;
  //   updateTimestamp();
  //   // eslint-disable-next-line
  // }, []);

  // React.useEffect(() => {
  //   // if (!signedIn) return;
  //   console.log(timestamp);

  //   // eslint-disable-next-line
  // }, [timestamp]);

  // const [unmounted, setUnmounted] = useState(false);

  // const onIdleTimerUnmount = () => {
  //   setIdleTimerUnmounted(true);
  // };

  /**
   * Data consistency tests
   */
  // if (accountNumber === null) return <>{t("Something went wrong")}</>;

  // const idleTimer = useIdleTimer({ ...args, onTimeout: onSessionTimeout });
  // <IdleTimer {...args} onTimeout={onSessionTimeout} />

  // const topMenuBackColor = "var(--light)";

  const location = useLocation();

  // console.log("location:", location);

  const navigation = (
    <>
      <Container
        fluid
        style={{
          // backgroundColor: "green",
          paddingRight: "0",
          paddingLeft: "0",
        }}
      >
        <Row>
          {/*
           * Top Menu
           */}
          <Col>
            <Navbar
              // className="card-header"
              // className="navbar-trans"
              // bg="light"
              // variant="light"
              style={{
                borderRadius: "5px",
                // background: "transparent !important",
                // color: "#f7f7f7",
                backgroundColor: "var(--light)", // e8f6f3
                boxShadow: "none",
                // whiteSpace: "nowrap",
              }}
            >
              <Navbar.Brand href="/">
                <img
                  src="gfx/zlogo_light.png"
                  width="auto"
                  height="30"
                  className="d-inline-block align-top"
                  alt={t("Logo")}
                />
              </Navbar.Brand>
              <Nav className="mr-auto">
                <Nav.Link eventKey="profile" href="/profile">
                  <UserEditor {...args} />
                </Nav.Link>
              </Nav>
              <Nav>
                <Nav.Link eventKey="sign-out" href="/sign-out">
                  <span style={{ whiteSpace: "nowrap" }}>
                    <span>
                      <PowerSettingsNew style={{ color: "var(--dark)" }} />
                    </span>

                    <span className="d-none d-sm-inline">
                      &nbsp;
                      {t("Sign out")}
                    </span>
                  </span>
                </Nav.Link>
              </Nav>
              {true && (
                <Nav>
                  <Nav.Item>
                    <IdleTimer {...args} />
                  </Nav.Item>
                </Nav>
              )}
            </Navbar>
          </Col>
        </Row>
        <Row>
          <Col>
            <div style={{ height: "7px" }}></div>
            <Navbar
              collapseOnSelect
              expand="sm"
              // className="card-header"
              // className="navbar-trans"
              // bg="dark"
              variant="dark"
              style={{
                borderRadius: "5px",
                background: "transparent !important",
                backgroundColor: "var(--dark)",
                boxShadow: "none",
              }}
            >
              <Nav>
                <Nav.Link
                  className="nav-link-2"
                  eventKey="balance"
                  href="/balance"
                  style={{
                    fontWeight:
                      location.pathname === "/balance" ? "bold" : "normal",
                  }}
                >
                  {t("Balance")}
                </Nav.Link>
              </Nav>
              <Navbar.Toggle aria-controls="responsive-navbar-nav" />
              <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="mr-auto">
                  {accountNumber !== null && (
                    <>
                      <Nav.Link
                        className="nav-link-2"
                        eventKey="transfer"
                        href="/transfer"
                        style={{
                          fontWeight:
                            location.pathname === "/transfer"
                              ? "bold"
                              : "normal",
                        }}
                      >
                        {t("Transfer")}
                      </Nav.Link>
                      <Nav.Link
                        className="nav-link-2"
                        eventKey="my-business"
                        // href="/my-business"
                        onClick={getBusinesses}
                        style={{
                          fontWeight:
                            location.pathname === "/my-business"
                              ? "bold"
                              : "normal",
                        }}
                      >
                        {t("Business")}
                      </Nav.Link>
                      <Nav.Link
                        className="nav-link-2"
                        eventKey="currency"
                        href="/currency"
                        style={{
                          fontWeight:
                            location.pathname === "/currency"
                              ? "bold"
                              : "normal",
                        }}
                      >
                        {t("Currency")}
                      </Nav.Link>
                    </>
                  )}
                </Nav>
              </Navbar.Collapse>
            </Navbar>
          </Col>
        </Row>
      </Container>
    </>
  );

  const viewBalanceOrTransfer = (
    <Router>
      <Switch>
        <Route
          exact
          path="/"
          component={() => {
            return accountNumber !== null
              ? Balance(args)
              : AccountRegistration(args);
          }}
        />
        <Route
          exact
          path="/summary"
          component={() => {
            return accountNumber !== null
              ? Balance(args)
              : AccountRegistration(args);
          }}
        />
        <Route
          exact
          path="/account-registration"
          component={() => {
            return AccountRegistration(args);
          }}
        />
        <Route
          exact
          path="/profile"
          component={() => {
            return UserEditor({ ...args, mode: "profile" });
          }}
        />
        <Route
          exact
          path="/currency"
          component={() => {
            return CurrencyContainer({ ...args });
          }}
        />
        {/* 
        <Route
          exact
          path="/business-registration"
          component={() => {
            return BusinessRegistration(args);
          }}
        />
        */}
        <Route
          exact
          path="/my-business"
          component={() => {
            return MyBusiness(args);
          }}
        />
        <Route
          exact
          path="/balance"
          component={() => {
            return Balance(args);
          }}
        />
        <Route
          exact
          path="/transfer"
          component={() => {
            return Transfer(args);
          }}
        />
      </Switch>
    </Router>
  );

  return (
    <>
      {/* Header */}

      {/* Content */}
      {/*<h3>{t("Summary")}</h3>*/}

      <Container fluid="md">
        <Row>
          <Col>
            {navigation}
            <div style={{ height: "7px" }}></div>
          </Col>
        </Row>
        {/*
        <Row>{viewAccountRegistration}</Row>
        */}
        <Row>
          <Col>{viewBalanceOrTransfer}</Col>
        </Row>
      </Container>
    </>
  );
};
