/**
 * System imports
 ============================================================================ */
import React, { useState } from "react";
// import { useTranslation } from "react-i18next";
import { Button, Spinner } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
import { useTimeout } from "../../utils/useTimeout";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    caption: "Submit",
    request: null,
    namespace: "Button",
    visible: true,
    marginTop: 0,
    timeout: 7000, // milliseconds
    onSubmit: () => {},
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { request } = props.barrelGetters;
  const { setRequest } = props.barrelSetters;
  const { showMessage } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  // const { t } = useTranslation([props.namespace]); // adjust namespace

  const [requestExpired, setRequestExpired] = useState(false);

  useTimeout(() => {
    if (request !== props.request) return;
    console.log("useTimeout");

    // Your custom logic here
    // setSeconds(seconds - 1);
    setRequestExpired(true);
    setRequest(null);
    showMessage("REQUEST_EXPIRED", "error");
  }, props.timeout);

  // useEffect(() => {
  //   if (requestExpired)
  // }, [requestExpired]);

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    return !props.visible ? (
      <></>
    ) : (
      <Button
        type="submit"
        onClick={props.onSubmit}
        block
        disabled={request === props.request && !requestExpired}
        style={{ marginTop: props.marginTop + "px" }}
      >
        <Spinner
          as="span"
          animation="border"
          size="sm"
          role="status"
          aria-hidden="true"
          hidden={request !== props.request || requestExpired}
        />
        &nbsp;
        {props.caption}
        &nbsp;
      </Button>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
